import selfBg from '@/components/self-bg.vue';
import selfContent from '@/components/self-content.vue';
import selfSteps from '@/components/self-steps.vue';
import selfButton from '@/components/self-button.vue';
import selfInput from '@/components/self-input.vue';
import selfCheckbox from '@/components/self-checkbox.vue';
import selfAgree from '@/components/self-agree.vue';
import selfRadio from '@/components/self-radio.vue';
import timer from '@/components/timer/index.vue';
import GetDate from '@/components/timer/GetDate.js';
import { guestGetTellCode, guestFirst, guestSecond, guestThree, changeGuestThree } from '@/api/login.js';

export default{
	data() {
		return {
			lists: [
				{ id: 1, text: '验证手机号' },
				{ id: 2, text: '完善个人信息' },
				{ id: 3, text: '完善来访信息' }
			],
			active: 1,
			chekck: false,
			isShow: false,
			options: [
				{ id: 1, label: '个人访客' },
				{ id: 2, label: '企业访客' }
			],
			visitorType: 1,
			array: ['观摩', '学习', '请教'],
			index: 0,
			address: ['猪场A', '猪场B', '猪场C'],
			idx: 0,
			visitDate: '',
			phone: '',
			smsCode: '',
			name: '',
			idNo: '',
			companyName: '',
			number: 60,
			isBegin: 1,
			accompanying: [],
			accShow: false,
			othserUser: '',
			othserSfz: '',
			type: null
		}
	},
	components: {
		selfBg,
		selfContent,
		selfSteps,
		selfButton,
		selfInput,
		selfCheckbox,
		selfAgree,
		selfRadio,
		timer
	},
	onLoad(options) {
		if(options && options.detail) {
			let detail = JSON.parse(options.detail)
			const { type, visitorType, visitors, reason, companyName, visitDate, targetLocation, phone, mainVisitors } = detail
			this.type = type
			if(type && type == 'change') {
				this.active = 3;
				this.visitorType = visitorType;
				this.accompanying = visitors
				this.index = this.array.findIndex(e=>e === reason);
				this.companyName = companyName;
				this.visitDate = visitDate;
				this.idx = this.address.findIndex(e=>e === targetLocation);
				this.phone = '13738051234';
				this.name = mainVisitors[0].name;
				this.idNo = mainVisitors[0].idNo;
				return
			}
		}
	    
		let time = GetDate.getCurrentTimes();
		let arr = [];
		for (let key in time.detail) {
			arr.push(time.detail[key]);
			if(key === this.fields){
				break;
			}
		}
		this.visitDate = GetDate.format(arr);
	},
	methods: {
		nextClick() {
			if(this.active == 1) {
				if(!this.phone) {
					uni.showToast({ title: '请输入手机号', icon: 'none' });
					return
				}
				if(!/^\d{11}$/.test(this.phone)) {
					uni.showToast({ title: '手机格式错误', icon: 'none' });
					return
				}
				if(!this.smsCode) {
					uni.showToast({ title: '请输入验证码', icon: 'none' });
					return
				}
				guestFirst({ phone: this.phone, smsCode: this.smsCode }).then(e=>{
					if(e.success) {
						this.active = ++this.active
					}
				})
			}
			if(this.active == 2) {
				if(!this.name) {
					uni.showToast({ title: '请输入姓名', icon: 'none' });
					return
				}
				if(!this.idNo) {
					uni.showToast({ title: '请输入身份证', icon: 'none' });
					return
				}
				// {"phone":"13738051234","idNo":"330727199011171234","name":"张五"}
				guestSecond({ phone: this.phone, idNo: this.idNo, name: this.name }).then(e=>{
					if(e.success) {
						this.active = ++this.active
					}
				})
			}
			if(this.active == 3) {
				if(this.visitorType == 2 && !this.companyName) {
					uni.showToast({ title: '请输入企业名称', icon: 'none' });
					return
				}
				if(!this.chekck) {
					uni.showToast({ title: '请阅读用户协议', icon: 'none' });
					return
				}
				const detail = {
					visitorType: this.visitorType,
					visitors: this.accompanying,
					reason: this.array[this.index],
					companyName: this.companyName,
					visitDate: this.visitDate,
					targetLocation: this.address[this.idx],
					phone: this.phone
				}
				this.type !== 'change' && guestThree(detail).then(e=> {
					detail.mainVisitors = [{
						name: this.name,
						idNo: this.idNo
					}]
					e.success && uni.navigateTo({
						url: `/pages/guestInfo/index?detail=${JSON.stringify(detail)}`
					})
				})
				this.type === 'change' && changeGuestThree(detail).then(e=> {
					detail.mainVisitors = [{
						name: this.name,
						idNo: this.idNo
					}]
					e.success && uni.navigateTo({
						url: `/pages/guestInfo/index?detail=${JSON.stringify(detail)}`
					})
				}) 
			}
		},
		selectHandle() {
			this.chekck = !this.chekck
		},
		agreeHandle() {
			this.isShow = true
		},
		closeHandle() {
			this.isShow = false
		},
		changeHandle(e) {
			this.visitorType = e.id
			this.companyName = ''
		},
		bindPickerChange(e) {
			this.index = e.target.value
		},
		addressChange(e) {
			this.idx = e.target.value
		},
		bindChange(e) {
			this.visitDate = e
		},
		getCodeHandle() {
			if(this.isBegin != 1) return
			this.isBegin = 2;
			guestGetTellCode({"phone": this.phone}).then(e=> {
				if(e && e.code === 10000) {
					this.isBegin = 3;
					let selfTimer = setInterval(()=>{
						this.number = --this.number
						if(this.number < 0) {
							this.number = 60
							this.isBegin = 1;
							clearInterval(selfTimer)
							selfTimer = null
						}
					}, 1000)
				} else {
					this.isBegin = 1;
					if(!e.success && e.code == 4107) {
						uni.showModal({
						    title: '很抱歉',
						    content: '暂无找到该手机号关联的邀请请联系邀请人核实信息',
							showCancel: false,
							confirmColor: '#02BB00'
						});
					}
				}
				
			})
		},
		addUserHandle() {
			this.accShow = true
		},
		addHandle() {
			if(!this.othserUser) {
				uni.showToast({ title: '请输入姓名', icon: 'none' });
				return
			}
			if(!this.othserSfz) {
				uni.showToast({ title: '请输入身份证', icon: 'none' });
				return
			}
			this.accompanying.push({name: this.othserUser, idNo: this.othserSfz})
			this.othserUser = '';
			this.othserSfz = '';
			this.accShow = false
		},
		deleteHandle(e, index) {
			this.accompanying.splice(index, 1);
		},
		toWorkHandle() {
			uni.navigateTo({ url: '/pages/workLogin/index' });
		}
	}
}
